@import '../utils/variables';
@import '../utils/mixins';
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    vertical-align: baseline;
    background: transparent;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}

body {
    line-height: 1.5;
}

a {
    text-decoration: none;
}

ul,
ol {
    list-style-type: none;
}

:is(h1,h2,h3,h4,.main-home>.btn>.head,.president>p) {
    font-weight: 600;
    text-align: center;
}

:is(h1,h2,h3,h4) {
    text-transform: uppercase;
}

h1 {
    font-size: 26px;
}

h2,
.head {
    font-size: 24px;
}

.card-text>h2 {
    font-size: 18px;
}

h3 {
    font-size: 22px;
}

h4,
.president>h3 {
    font-size: 20px;
}

.president>h3{
    font-weight: 400;
}

:is(p,span,.service-c) {
    font-size: 16px;
    letter-spacing: .5px;
    text-align: left;
}

label,
.btn-card {
    font-size: 12px;
}
.serv-c--m{
    p{
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 600;
    }
}